@import "../../global.scss";

.portfolio {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;

  h1 {
    font-size: 50px;

    @include mobile{
      font-size: 20px;
    }

    
  }

  ul {
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;  /*removed li styling from here to portfolioList*/

        @include mobile{
          flex-wrap: wrap;
          justify-content: center;
        }

    }

  .container {
    width: 60%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    @include mobile{
      width: 100%;
    }
   

    .item {
      width: 330px;
      height: 220px;
      border-radius: 20px;
      border: 1px solid rgb(240, 239, 239);
      margin: 10px 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      position: relative;
      transition: all .5s ease;
      cursor: pointer;

      @include mobile{
        width: 130px;
        height: 120px;
      }

      

      h3 {
        position: absolute;
        font-size: 20px;
        
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 1;
      }

      &:hover {
        background-color: $mainColor;
        img {
          opacity: 0.2;
          z-index: 0;
        }
      }
    }
  }
}